.explore-menu{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.explore-menu h1{
    color: #262626;
    font-weight: 500;
}
.explore-menu-text{
    max-width: 60%;
    color: #808080;
}
.explore-menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 30px 0;
    overflow-x: scroll;
}
.explore-menu-list::-webkit-scrollbar{
    display: none;   
}
.explore-menu-list-item img{
    width: 7.5vw;
    min-width: 80px;
    cursor: pointer;
    border-radius: 50%;
    transition: 0.2s;
}
.explore-menu-list-item img:hover{
    transform: scale(1.02);
}
.explore-menu-list-item p{
    margin-top: 10px;
    cursor: pointer;
    color: #747474;
    font-size: max(1.4vw, 16px);
}
.explore-menu-list-item hr{
    margin: 10px;
    height: 2px;
    background-color: #e2e2e2;
    border: none;
}
.explore-menu-list-item .active{
    border: 4px solid tomato;
    padding: 2px;
}

@media(max-width:1050px){
    .explore-menu-text{
        max-width: 100%;
        font-size: 14px;
    }
}