.footer{
    color: #d9d9d9;
    background-color: #323232;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px 8vw;
    padding-top: 80px;
    margin-top: 100px;
}
.footer-content{
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 80px;
}
.footer-content-left, .footer-content-right, .footer-content-center{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 20px;
} 
.footer-content-left li, .footer-content-right li, .footer-content-center li{
    list-style-type: none;
    margin-bottom: 10px;
    cursor: pointer;
}
.footer-content-right h2, .footer-content-center h2{
    color: white;
}
.footer-social-icons img{
    width:40px;
    margin-right:15px;
    cursor: pointer;
}

.footer-logo{
    width: 140px;
}
.footer hr{
    width: 100%;
    height: 2px;
    margin: 20px 0;
    border: none;
    background-color: gray;
}
@media(max-width:750px){
    .footer-content{
        gap: 35px;
        display: flex;
        flex-direction: column;
    }
    .footer-content-center{
        text-align: center;
    }
    .footer-content-left{
        text-align: justify;
    }
}